import { Component, h, Prop, Element, State, Method } from '@stencil/core';

@Component({
  tag: 'ks-order-document-select',
})
export class OrderDocumentSelect {

    @Element() root: HTMLElement;
    @Prop() name: string;
    @Prop() api: string;

    @Prop() chooseSaleDocument: string;
    @Prop() receipt: string;
    @Prop() invoice: string;

    @State() loading: boolean;

    componentDidLoad(){
        this.root.style.display = "block"

        const radios = this.root.querySelectorAll(`input[name=${this.name}]`);
        radios.forEach((radio)=>{
            radio.addEventListener("change", (e)=>{
                const target = e.target as HTMLInputElement;

                const form = new FormData();
                form.append("value", target.value);

                var headers = new Headers();
                headers.append('pragma', 'no-cache');
                headers.append('cache-control', 'no-cache');

                this.loading = true;

                fetch(this.api, {
                    method: 'POST',
                    headers: headers,
                    body: form,
                    credentials: "same-origin"
                })
                    .then(()=>this.loading=false);
            })
        })
    }

    @Method()
    async Validate() {}

    @Method()
    async IsValid() {
        return !this.loading;
    }

    render() {
        return[
            <span>{this.chooseSaleDocument}</span>,
            <div class="uk-position-relative uk-margin-small-top">
                <div class="uk-child-width-1-2 uk-flex">
                    <ks-order-toggle active text={this.receipt} name={this.name} value="0"></ks-order-toggle>
                    <ks-order-toggle text={this.invoice} name={this.name} value="1"></ks-order-toggle>
                </div>
                {this.loading ?
                    <div class="uk-overlay uk-overlay-default uk-padding-remove uk-position-cover uk-animation-fade"
                         style={{animationDuration: "0.3s", animationDelay: "0.5s"}}>
                        <div uk-spinner class="uk-position-center"></div>
                    </div>
                : null}
            </div>
            
        ];
    }
}